import { useState } from "react";
import { SignUpContainer, Form, Input, H1, Label } from "./SignUpEmailStyle";
import BtnStyle from "../../../components/Button/Button";

export default function SignUpEmail({ 
  setPage,
  email,
  setEmail,
  password,
  setPassword,}) {
  const [emailValid, setEmailValid] = useState(false);
  const [passwordValid, setPasswordValid] = useState(true);
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");

  //  
  const handleEmailInput = (event) => {
    setEmail(event.target.value);
  };

  //   
  const handlePasswordInput = (event) => {
    const value = event.target.value;
    setPassword(value);
    if (value.length >= 6) {
      setPasswordError("");
      setPasswordValid(true);
    } else {
      setPasswordError(" 6  .");
      setPasswordValid(false);
    }
  };

  //   
  const handleEmailValid = async (event) => {
    const value = event.target.value;

    const postEmailValid = await fetch(`https://api.mandarin.weniv.co.kr/user/emailvalid`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        user: {
          email: value,
        },
      }),
    });

    const json = await postEmailValid.json();
    console.log(json.message);

    const Msg = json.message;
    setEmailError(Msg);
    Msg === "   ." ? setEmailValid(true) : setEmailValid(false);
  };

  // ,     
  const handleSubmit = (event) => {
    event.preventDefault();
    if (emailValid && passwordValid) {
    }
  };

  //   
  const handleForm = (e) => {
    e.preventDefault(); 
    setPage('SignUpProfile');
  };

  return (
    <>
      <SignUpContainer>
        <H1> </H1>
        <Form 
         onSubmit={handleSubmit}>
          <Label htmlFor='user-email'></Label>
          <Input id={"user-email"} type={"email"} label={""} placeholder={"   ."} value={email} valid={emailValid} alertMsg={setEmailError} onChange={handleEmailInput} onBlur={handleEmailValid} />
          {emailError && <p style={{ marginBottom: "2rem", marginTop: "-2.4rem", fontSize: "1.2rem", color: "var(--font-red-color)" }}>{emailError}</p>}
          <Label htmlFor='user-password'></Label>
          <Input id={"user-password"} type={"password"} label={""} placeholder={"  ."} value={password} valid={passwordValid} alertMsg={setPasswordError} onChange={handlePasswordInput} />
          {passwordError && <p style={{ marginBottom: "3rem", marginTop: "-2.4rem", fontSize: "1.2rem", color: "var(--font-red-color)" }}>{passwordError}</p>}
          {emailValid && passwordValid ? (
            <BtnStyle onClick={handleForm} type='submit'>
              
            </BtnStyle>
          ) : (
            <BtnStyle onClick={handleForm} type='submit' disabled>
              
            </BtnStyle>)}
        </Form>
      </SignUpContainer>
    </>
  );
}
